import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const CardWrapper = styled.div`
  margin: 0 2px;
  height: 100%;
  display: flex;
`;

const CardBorder = styled.div`
  position: relative;
  background-color: ${props => props.theme.cardBackgroundColor};
  display: block;
  border-radius: 4px;
  color: ${props => props.theme.cardTextColor};

  &:before {
    border-left: 2px solid ${props => props.theme.cardBorderColor};
    border-right: 2px solid ${props => props.theme.cardBorderColor};
    left: -2px;
    top: 2px;
    right: -2px;
    bottom: 2px;
  }

  &:after,&:before {
    content: "";
    display: block;
    position: absolute;
  }

  &:after {
    border-top: 2px solid ${props => props.theme.cardBorderColor};
    border-bottom: 2px solid ${props => props.theme.cardBorderColor};
    left: 2px;
    top: -2px;
    right: 2px;
    bottom: -2px;
  }
`;

const CardImage = styled.img`
  width: 100%;
  display: block;
  border-bottom: 2px solid ${props => props.theme.cardBorderColor};
`;

const CardContent = styled.div`
  padding: 10px 15px;
`;

const CardCorner = styled.div`
  display: block;
  position: absolute;
  margin: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 2px solid;
  border-image: linear-gradient(${props => props.theme.mainTextColor}, ${props => props.theme.mainTextColor})
`;

const Card = ({ image, imageAlt, children }) => {
  return (
    <CardWrapper>
      <CardBorder>
        <CardImage src={image} alt={imageAlt} />
        <CardContent>
          {children}
        </CardContent>

        <CardCorner />
      </CardBorder>
    </CardWrapper>
  )
};

Card.propTypes = {
  image: PropTypes.string.isRequired,
  imageAlt: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired
};

export default Card;
