import styled from 'styled-components';
import PropTypes from 'prop-types';

import { mediaQueries } from '@styles/media';

const calcPercent = (col) => col / 12 * 100;

const FlexColumn = styled.div`
  box-sizing: border-box;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  margin-bottom: 1.5rem;
  flex: 0 0 ${props => calcPercent(props.xs) || 100}%;
  max-width: ${props =>  calcPercent(props.xs) || 100}%;

  @media ${mediaQueries.tablet} {
    flex: 0 0 ${props => calcPercent(props.sm) || calcPercent(props.xs) || 100}%;
    max-width: ${props =>  calcPercent(props.sm) || calcPercent(props.xs) || 100}%;
  }

  @media ${mediaQueries.desktop} {
    flex: 0 0 ${props => calcPercent(props.md) || calcPercent(props.sm) || calcPercent(props.xs) || 100}%;
    max-width: ${props =>  calcPercent(props.md) || calcPercent(props.sm) || calcPercent(props.xs) || 100}%;
  }

  @media ${mediaQueries.largerScreen} {
    flex: 0 0 ${props => calcPercent(props.lg) || calcPercent(props.md) || calcPercent(props.sm) || calcPercent(props.xs) || 100}%;
    max-width: ${props =>  calcPercent(props.lg) || calcPercent(props.md) || calcPercent(props.sm) || calcPercent(props.xs) || 100}%;
  }
`;

FlexColumn.propTypes = {
  xs: PropTypes.number.isRequired,
  sm: PropTypes.number,
  md: PropTypes.number,
  lg: PropTypes.number,
};

export default FlexColumn;
